// Mobile first
@custom-media --gte-sm (width >= env(--sm));
@custom-media --gte-md (width >= env(--md));
@custom-media --gte-lg (width >= env(--lg));
@custom-media --gte-xl (width >= env(--xl));

// Desktop first
@custom-media --lt-xl (width < env(--xl));
@custom-media --lt-lg (width < env(--lg));
@custom-media --lt-md (width < env(--md));
@custom-media --lt-sm (width < env(--sm));

// Grid
$grid-breakpoints: (
	xs: env(--xs),
	sm: env(--sm),
	md: env(--md),
	lg: env(--lg),
	xl: env(--xl)
);

$container-max-widths: (
	lg: env(--lg)
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}








































































































.catalogue-item {
	@include reset;
}

.row {
	margin-bottom: -2rem;
}

.col {
	margin-bottom: 2rem;
}

.content {
	margin-bottom: 3rem;
}

.sect-nav {
	margin-bottom: 3rem;
}

.img-items {
	display: flex;
	align-items: flex-start;
	justify-content: center;
	margin: -1rem;
}

.img-item {
	@include reset;

	position: relative;
	padding: 1rem;
	max-width: calc(100% - 200px);

	@media (--lt-sm) {
		max-width: calc(100% - 150px);
	}

	&:nth-child(2) {
		margin-top: 120px;

		.img-item__caption {
			left: auto;
			right: 100%;
			top: auto;
			bottom: 0;
			padding-top: 4.5rem;
			padding-bottom: 0;
			transform: translate(1rem, -1rem);

			&::before {
				top: 0;
				bottom: auto;
			}

			&::after {
				top: 0;
				bottom: auto;
				transform: rotateY(180deg);
				transform-origin: 100% 50%;
			}
		}
	}

	&__elem {
		display: block;
		width: 100%;
	}

	&__caption {
		@include reset;

		position: absolute;
		left: 100%;
		top: 0;
		z-index: 1;
		padding-bottom: 4.5rem;
		min-width: 200px;
		max-width: 100%;
		font-size: var(--content-font-size);
		line-height: 1.25;
		text-align: center;
		color: var(--content-color);
		transform: translate(-1rem, 1rem);

		@media (--lt-sm) {
			min-width: 150px;
			font-size: 14px;
		}

		&::before {
			content: "";
			position: absolute;
			right: 50%;
			bottom: 0;
			width: 1px;
			height: 4rem;
			background-color: var(--content-color);
		}

		&::after {
			content: "";
			position: absolute;
			right: 50%;
			bottom: 0;
			width: 4rem;
			height: 1px;
			background-color: var(--content-color);
		}
	}

	&--single {
		max-width: 100%;

		@media (--lt-sm) {
			max-width: 100%;
		}
	}
}
