// Mobile first

// Desktop first

// Grid
$grid-breakpoints: (
	xs: 0,
	sm: 640px,
	md: 960px,
	lg: 1280px,
	xl: 1600px
);

$container-max-widths: (
	lg: 1280px
);

$grid-gutter-width: 2rem;
@function env($variable) {
	@error "\"env()\" function was not provided by PostCSS or dollar-variable was used as passed argument";
}

@function media($query...) {
	@error "\"media()\" function was not provided by PostCSS or was called without arguments";
}
@mixin clearfix {
	&::after {
		content: "";
		display: table;
		clear: both;
	}
}
%reset {
	all: initial;

	// Properties dependent of browser
	quotes: "“" "”" "‘" "’";
	font-family: var(--content-font-family);
	text-align: left;
	color: var(--content-color);
	outline-color: var(--content-color);

	&:focus-visible {
		outline: 1px dotted currentColor;
		outline: 5px auto -webkit-focus-ring-color;
	}

	// Overwrite some properties
	display: block;
	box-sizing: border-box;
}

@mixin reset {
	@extend %reset;
}
@mixin retina {
	// stylelint-disable-next-line media-feature-name-no-vendor-prefix
	@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi) {
		@content;
	}
}
@mixin triangle($direction, $width, $height, $color) {
	$top-width: 0;
	$top-color: rgba(0, 0, 0, 0);
	$right-width: 0;
	$right-color: rgba(0, 0, 0, 0);
	$bottom-width: 0;
	$bottom-color: rgba(0, 0, 0, 0);
	$left-width: 0;
	$left-color: rgba(0, 0, 0, 0);

	@if $direction == (to top) {
		$left-width: $width / 2;
		$right-width: $width / 2;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to top right) or $direction == (to right top) {
		$right-width: $width;
		$right-color: $color;
		$bottom-width: $height;
	} @else if $direction == (to right) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to bottom right) or $direction == (to right bottom) {
		$left-width: $width;
		$bottom-width: $height;
		$bottom-color: $color;
	} @else if $direction == (to bottom) {
		$top-width: $height;
		$top-color: $color;
		$left-width: $width / 2;
		$right-width: $width / 2;
	} @else if $direction == (to bottom left) or $direction == (to left bottom) {
		$top-width: $height;
		$left-width: $width;
		$left-color: $color;
	} @else if $direction == (to left) {
		$top-width: $height / 2;
		$bottom-width: $height / 2;
		$right-width: $width;
		$right-color: $color;
	} @else if $direction == (to top left) or $direction == (to left top) {
		$top-width: $height;
		$top-color: $color;
		$right-width: $width;
	} @else {
		$direction: false;

		@warn "Incorrect triangle direction: #{inspect($direction)}";
	}

	@if $direction {
		border-style: solid;
		border-width: $top-width $right-width $bottom-width $left-width;
		border-color: $top-color $right-color $bottom-color $left-color;
		width: 0;
		height: 0;
	}
}
@mixin visually-hidden {
	position: absolute;
	overflow: hidden;
	margin: -1px;
	width: 1px;
	height: 1px;
	clip: rect(0 0 0 0);
}


































































































































































.catalogue-sect {
	@include reset;

	position: relative;
	overflow: hidden;
	padding: 12rem 0;
	background-color: var(--page-background-color);

	@media (max-width: 959px) {
		padding: 3rem 0;
	}

	&::before {
		content: "";
		position: absolute;
		left: 50%;
		top: 0;
		width: 1px;
		height: 0;
		background-color: #e0e0e0;
		transform: translate(0, 3rem);
		transition: height 1500ms ease-out;

		@media (max-width: 959px) {
			content: normal;
		}
	}

	&::after {
		content: "";
		position: absolute;
		left: 50%;
		top: 100%;
		width: 1px;
		height: 0;
		background-color: #e0e0e0;
		transform: translate(0, -6rem) translate(0, -3rem);
		transition: height 1500ms ease-out;

		@media (max-width: 959px) {
			content: normal;
		}
	}

	&.animate--started,
	&.animate--completed {
		&::before,
		&::after {
			height: 6rem;
		}
	}
}

.header {
	margin-bottom: 3rem;

	&__title {
		@include reset;

		margin: 0 0 0.5em;
		font-weight: var(--content-headings-font-weight);
		font-size: var(--content-headings-font-size);
		line-height: var(--content-headings-line-height);
		color: var(--content-headings-color);
	}

	&__desc {
		@include reset;

		font-weight: 500;
		font-size: var(--content-font-size);
		line-height: 1.25;
		color: var(--content-headings-color);
	}
}

.catalogue-item {
	& + & {
		margin-top: 6rem;

		@media (max-width: 959px) {
			margin-top: 3rem;
		}
	}
}
